<template>
    <div id="content"></div>
</template>


<script setup>
    import * as echarts from "echarts";
    import china from '@/assets/china1.json'
    import { onMounted, reactive } from "vue";
    import { useRouter } from "vue-router";
    import $ from "jquery";
    import router from '@/router'

    var points = [ // 
          { name: '',position:'', value: [87.628579, 43.793301], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [104.076452, 30.651696], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [103.826777, 36.060634], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [102.709372, 25.046432], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [108.327537, 22.816659], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [112.982951, 28.116007], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [117.020725, 36.670201], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [113.753094, 34.767052], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [112.578781, 37.813948], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [119.296194, 26.101082], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [120.152575, 30.266619], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [118.763563, 32.061377], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [116.407387, 39.904179], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [108.953939, 34.266611], itemStyle: { color: '#00EEFF' } }, // 
          { name: '', position:'',value: [113.266887, 23.133306], itemStyle: { color: '#00EEFF' } } // 
        ];


        onMounted(()=>{
            let container = document.getElementById("content");
            var myChart = echarts.init(container);
            var option = {
                tooltip:{
                    show:true,
                    trigger:'item',
                    triggerOn: "mousemove",
                    enterable:true,
                    alwaysShowContent: false,
                    backgroundColor: "#0C121C",
                    borderColor: "rgba(0, 0, 0, 0.16);",
                    formatter:function(params){
                        var str = "";
                        if(params.name){
                            str += '<div class = "point">'+params.name+'</div>'   
                        }
                        return str;
                    },
                    textStyle: {
                        color: "#DADADA",
                        fontSize: "12",
                        width: 20,
                        height: 30,
                        overflow: "break",
                    },
                },

                // legend:{
                //     orient:'vertical',
                //     left:'10%',
                //     data:['', '','','','','','','','','','','','','','',]
                    
                // },

                geo: {// 
                    map: 'china',
                    roam:false,//
                    zoom:1.2,
                    label: { // 
                        normal: {// 
                            show: false,
                            textStyle: {
                            color: '#fff'
                            }
                        },
                        emphasis: {// 
                            show:false,
                            textStyle: {
                            color: '#fff'
                            }
                        }
                    },
                     // 
                    itemStyle: {
                        borderColor: "rgba(147, 235, 248, 1)",
                        borderWidth: 1,
                        areaColor: {
                            type: "radial",
                            x: 0.5,
                            y: 0.5,
                            r: 0.8,
                            colorStops: [
                            {
                                offset: 0,
                                color: "rgba(147, 235, 248, 0)", // 0% 
                            },
                            {
                                offset: 1,
                                color: "rgba(147, 235, 248, .2)", // 100% 
                            },
                            ],
                            globalCoord: false,
                        },
                        shadowColor: "rgba(128, 217, 248, 1)",
                        shadowOffsetX: -2,
                        shadowOffsetY: 2,
                        shadowBlur: 10,
                    },
                    emphasis: {
                        areaColor: '#389BB7',
                        borderWidth: 0
                    }
                },
                
                series: [
                    { // 
                    type: 'effectScatter',// 
                    coordinateSystem: 'geo', //:
                    // ,'ripple'
                    effectType: 'ripple',
                    // 'render''emphasis' 
                    showEffectOn: 'emphasis',
                    rippleEffect: { // 
                        period: 3, // 
                        scale: 4,// 
                        //  'stroke'  'fill'
                        brushType: 'fill'
                    },
                    zlevel: 1, //  zlevel 
                    data: points,
                    
                    },
                ],

                
                
                
            }

            echarts.registerMap('china',china);
            myChart.setOption(option);


            
            //
            // const router = useRouter();
            myChart.on("click", function (params) {
                if(params.seriesType === "effectScatter"){
                    // router.push({
                    //     path: "/detail",
                    // }); 
                    router.push("/detail");
                }
                // console.log(params.data);
            })



            //,
            let resizeObserver = new ResizeObserver(()=>myChart?.resize());
            resizeObserver.observe(container);
        })


</script>



<style lang="less">
 #content{
    width: 100%;
    height: 100%;
    margin:-20px auto;
  }


  //
  .point{
    color:#00cccc;
  }
</style>